                        survey: this.checkedSurvey,
                        idle: this.checkedIdle,
                        idle_popup: this.idle_popup,
                        idle_timeout: this.idle_timeout,
                        idle_debug: this.idle_debug,
                        pricing_type: this.checkedPricing, //  pricing_type
